___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}
html {
  background-color: #22d3ee;
}
#container {
  width: 100vw;
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#subContainer {
  width: 100%;
  height: 100%;
  background-color: #22d3ee;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  border-radius: 10px;
}
#searchContainer {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#searchBar {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: white;
  outline: none;
  border: none;
  font-size: 24px;
  padding-left: 6px;
  position: relative;
}
#searchButton {
  height: 36px;
  margin-left: -38px;
  border: none;
  border-radius: 10px;
  background-color: #f97316;
  color: white;
  position: absolute;
  right: 0;
}
#searchButton:hover {
  background-color: #fb923c;
}

#gifBox {
  width: 100%;
  height: fit-content;
  border-radius: 10px;
  background-color: #67e8f9;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.gif-image {
  max-width: 100%;
}

@media screen and (min-width: 850px) {
  #subContainer {
    width: fit-content;
    height: fit-content;
    background-color: #67e8f9;
  }
  #gifBox {
    width: 500px;
    height: 500px;
  }
}
