import "./style.css"

const components = (() => {
  const subContainer = () => {
    const container = document.createElement("div")
    container.id = "subContainer"
    return container
  }

  const searchContainer = () => {
    const container = document.createElement("div")
    container.id = "searchContainer"
    return container
  }

  const searchBar = () => {
    const input = document.createElement("input")
    input.type = "text"
    input.id = "searchBar"
    return input
  }

  const searchButton = () => {
    const button = document.createElement("button")
    button.id = "searchButton"
    button.innerHTML = `<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="currentColor" d="m19.6 21l-6.3-6.3q-.75.6-1.725.95T9.5 16q-2.725 0-4.612-1.888T3 9.5q0-2.725 1.888-4.612T9.5 3q2.725 0 4.612 1.888T16 9.5q0 1.1-.35 2.075T14.7 13.3l6.3 6.3l-1.4 1.4ZM9.5 14q1.875 0 3.188-1.313T14 9.5q0-1.875-1.313-3.188T9.5 5Q7.625 5 6.312 6.313T5 9.5q0 1.875 1.313 3.188T9.5 14Z"/></svg>`
    return button
  }

  const gifBox = () => {
    const gifBox = document.createElement("div")
    gifBox.id = "gifBox"
    return gifBox
  }

  const PROJECT_CONTAINER = document.getElementById("container")

  const appendComponenents = () => {
    PROJECT_CONTAINER.appendChild(subContainer())
    const container = document.getElementById("subContainer")
    container.appendChild(searchContainer())
    const searchDiv = document.getElementById("searchContainer")
    searchDiv.appendChild(searchBar())
    searchDiv.appendChild(searchButton())
    container.appendChild(searchDiv)
    container.appendChild(gifBox())
  }
  return { appendComponenents }
})()

const api = (() => {
  const getSearchText = () => {
    const text = document.getElementById("searchBar").value
    return text
  }

  const removeAllChild = parent => {
    while (parent.firstChild) {
      parent.removeChild(parent.firstChild)
    }
  }

  async function getData() {
    const API_KEY = process.env.API_KEY
    const API_URL = `https://api.giphy.com/v1/gifs/search?api_key=${API_KEY}&q=${getSearchText()}&limit=25&offset=0&rating=g&lang=en`
    try {
      const response = await fetch(API_URL)
      if (!response.ok) {
        throw new Error(`HTTP ERROR: ${response.status}`)
      }
      const data = await response.json()
      const objArr = data.data
      const urlArr = objArr.map(obj => obj.images.original.url)
      return urlArr
    } catch (error) {
      console.log(error)
    }
  }

  async function displayGifs() {
    removeAllChild(document.getElementById("gifBox"))
    try {
      const urlArr = await getData()
      const container = document.getElementById("gifBox")
      urlArr.forEach(url => {
        const img = document.createElement("img")
        img.src = url
        img.classList.add("gif-image")
        container.appendChild(img)
      })
    } catch (error) {
      console.error(error)
    }
  }

  return { displayGifs }
})()

const events = (() => {
  const searchButtonPressed = () => {
    const button = document.getElementById("searchButton")
    button.addEventListener("click", api.displayGifs)
  }

  const enterButtonPressed = () => {
    const searchBar = document.getElementById("searchBar")
    const searchButton = document.getElementById("searchButton")
    searchBar.addEventListener("keyup", e => {
      if (e.key == "Enter") {
        searchButton.click()
      }
    })
  }

  const initListeners = () => {
    searchButtonPressed()
    enterButtonPressed()
  }

  return { initListeners }
})()

components.appendComponenents()
events.initListeners()
